.App {
  text-align: center;
}
.logo {
  width: 100px;
  margin: 50px 0px;
}
.main {
  width: 100%;
  height: 700px;
  background: url(./main.gif) no-repeat center / cover;
}
.row {
  width: 1200px;
  margin: 0px auto;
}
.img {
  width: 100%;
}
h3 {
  margin: 100px 0px 50px;
}
h4 {
  margin-top: 10px;
  font-weight: bold;
  font-size: 20px;
}
p:nth-of-type(1) {
  font-size: 14px;
}
p {
  color: #aaa;
}
.price {
  color: #000;
  font-weight: bold;
}
.order {
  font-size: 20px;
  width: fit-content;
  margin: 50px auto;
  padding: 5px 10px;
  background: lightblue;
  font-weight: bold;
  border-radius: 10px;
  cursor: pointer;
}
.row {
  align-items: center;
}
.nav {
  margin: 50px auto;
  justify-content: center;
}
.navitem {
  width: 200px;
}
.tabcontent {
  margin: 50px auto;
}
